.check-material input[type="checkbox"] {
	display: none;
}

.check-material input[type="checkbox"]:checked + label {
	background: #FFFFFF;
	border-color: #FFFFFF;
}

.check-material input[type="checkbox"]:checked + label:after {
	transform: scale(1.3);
}

.check-material label {
	display: block;
	box-sizing: border-box;
	width: 21px;
	height: 21px;
	background: #FFFFFF;
	border: 1px solid #59D554;
	border-radius: 3px;
	position: relative;
	cursor: pointer;
	transition: .2s;
	margin-bottom: 0px;
}

.check-material label:after {
	content: '';
	display: block;
	height: 100%;
	width: 100%;
	background: url(/img/check.svg) center center no-repeat;
	background-size: contain;
	border: 1px solid #59D554;
	border-radius: 3px;
	transform: scale(0);
	transition: .2s;
}

.check-material label:active:before {
	background: #FFFFFF;
}

.check-material-circle input[type="checkbox"] {
	display: none;
}

.check-material-circle input[type="checkbox"]:checked + label:after {
	transform: scale(1.3);
}

.check-material-circle label {
	display: block;
	box-sizing: border-box;
	width: 24px;
	height: 24px;
	background: #EEEEEE;
	border: 6px solid #EEEEEE;
	border-radius: 50%;
	position: relative;
	cursor: pointer;
	transition: .2s;
}

.check-material-circle label:after {
	content: '';
	display: block;
	width: 5px;
	height: 5px;
	border: 6px solid #59D554;
	border-radius: 50%;
	transform: scale(0);
	transition: .2s;
}

.check-material-circle label:active:before {
	background: #FFFFFF;
}

